'use client';

import { useState, useEffect, FormEvent } from 'react';
import { Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Switch } from '@/components/ui/switch';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
} from '@/components/ui/dialog';
import { ProdukResto, Kategori, Satuan } from '@/types';

interface ProdukFormDialog {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: (formData: any, isEdit: boolean) => Promise<void>;
    initialData: ProdukResto | null; // Data jika edit, null jika tambah
    kategoris: Kategori[];
    satuans: Satuan[];
}

export default function ProductFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    initialData,
    kategoris,
    satuans
}: ProdukFormDialog) {
    const [isSaving, setIsSaving] = useState(false);
    
    // Form State
    const [formData, setFormData] = useState({
        nama_produk: '',
        deskripsi: '',
        kategori_id: '',
        satuan_id: '',
        harga_jual: '',
        is_tersedia: true,
        foto: null as File | null,
    });

    useEffect(() => {
        if (isOpen && initialData) {
            setFormData({
                nama_produk: initialData.nama_produk,
                deskripsi: initialData.deskripsi || '',
                kategori_id: initialData.kategori_id.toString(),
                satuan_id: initialData.satuan_id.toString(),
                harga_jual: initialData.harga_jual.toString(),
                is_tersedia: Boolean(initialData.is_tersedia),
                foto: null
            });
        } else if (isOpen && !initialData) {
            setFormData({
                nama_produk: '',
                deskripsi: '',
                kategori_id: '',
                satuan_id: '',
                harga_jual: '',
                is_tersedia: true,
                foto: null
            });
        }
    }, [isOpen, initialData]);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);
        await onSubmit(formData, !!initialData);
        setIsSaving(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[600px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Produk' : 'Tambah Produk Baru'}</DialogTitle>
                    <DialogDescription>Isi detail produk restoran di bawah ini.</DialogDescription>
                </DialogHeader>
                
                <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2 col-span-2">
                            <Label>Nama Produk <span className="text-red-500">*</span></Label>
                            <Input 
                                value={formData.nama_produk}
                                onChange={(e) => setFormData({...formData, nama_produk: e.target.value})}
                                required
                            />
                        </div>
                        
                        {/* Dropdown Kategori */}
                        <div className="space-y-2">
                            <Label>Kategori <span className="text-red-500">*</span></Label>
                            <Select 
                                value={formData.kategori_id} 
                                onValueChange={(val) => setFormData({...formData, kategori_id: val})}
                            >
                                <SelectTrigger><SelectValue placeholder="Pilih..." /></SelectTrigger>
                                <SelectContent>
                                    {kategoris.map((k) => (
                                        <SelectItem key={k.id} value={k.id.toString()}>{k.nama_kategori}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        {/* Dropdown Satuan */}
                        <div className="space-y-2">
                            <Label>Satuan <span className="text-red-500">*</span></Label>
                            <Select 
                                value={formData.satuan_id} 
                                onValueChange={(val) => setFormData({...formData, satuan_id: val})}
                            >
                                <SelectTrigger><SelectValue placeholder="Pilih..." /></SelectTrigger>
                                <SelectContent>
                                    {satuans.map((s) => (
                                        <SelectItem key={s.id} value={s.id.toString()}>{s.nama_satuan}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        <div className="space-y-2">
                            <Label>Harga Jual (Rp) <span className="text-red-500">*</span></Label>
                            <Input 
                                type="number"
                                value={formData.harga_jual}
                                onChange={(e) => setFormData({...formData, harga_jual: e.target.value})}
                                required
                            />
                        </div>

                         <div className="space-y-2 flex flex-col justify-center">
                            <Label className="mb-2">Status Ketersediaan</Label>
                            <div className="flex items-center space-x-2">
                                <Switch 
                                    checked={formData.is_tersedia}
                                    onCheckedChange={(checked) => setFormData({...formData, is_tersedia: checked})}
                                />
                                <span className="text-sm text-gray-600">
                                    {formData.is_tersedia ? 'Tersedia' : 'Habis'}
                                </span>
                            </div>
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label>Deskripsi</Label>
                        <Textarea 
                            value={formData.deskripsi}
                            onChange={(e) => setFormData({...formData, deskripsi: e.target.value})}
                        />
                    </div>

                    <div className="space-y-2">
                        <Label>Foto Produk</Label>
                        <Input 
                            type="file" 
                            accept="image/*"
                            className="cursor-pointer"
                            onChange={(e) => {
                                if (e.target.files?.[0]) setFormData({ ...formData, foto: e.target.files[0] });
                            }}
                        />
                    </div>

                    <DialogFooter className="pt-4">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>Batal</Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} Simpan
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}