'use client';

import Image from 'next/image';
import Link from 'next/link';
import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight, ChefHat } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { ProdukResto } from '@/types';

interface ProdukTabelProps {
    loading: boolean;
    produks: ProdukResto[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (produk: ProdukResto) => void;
    onDelete: (id: number) => void;
}

export default function ProdukTabel({
    loading,
    produks,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: ProdukTabelProps) {
    
    const formatRupiah = (angka: number | string) => {
        return new Intl.NumberFormat('id-ID', { 
            style: 'currency', 
            currency: 'IDR', 
            minimumFractionDigits: 0 
        }).format(Number(angka));
    };

    const totalPages = Math.ceil(totalData / itemsPerPage);
    const startNumber = (currentPage - 1) * itemsPerPage + 1;

    return (
        <div className="rounded-md border bg-white shadow-sm">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[50px] text-center">No</TableHead>
                        <TableHead className="w-[80px]">Foto</TableHead>
                        <TableHead>Nama Produk</TableHead>
                        <TableHead>Kategori</TableHead>
                        <TableHead>Harga</TableHead>
                        <TableHead>Status</TableHead>
                        <TableHead className="text-right">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={7} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat...
                            </TableCell>
                        </TableRow>
                    ) : produks.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={7} className="h-32 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-8 w-8 text-gray-300 mb-2" />
                                    <p>Tidak ada produk yang cocok.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        produks.map((item, index) => (
                            <TableRow key={item.id} className="hover:bg-gray-50">
                                <TableCell className="text-center text-gray-500">
                                    {startNumber + index}
                                </TableCell>
                                <TableCell>
                                    <div className="relative h-10 w-10 rounded-md overflow-hidden bg-gray-100 border">
                                        {item.foto_url ? (
                                            <Image 
                                                src={item.foto_url} 
                                                alt={item.nama_produk} 
                                                fill 
                                                className="object-cover"
                                                unoptimized
                                                onError={(e) => (e.target as HTMLElement).style.display = 'none'}
                                            />
                                        ) : <div className="w-full h-full flex items-center justify-center text-xs">IMG</div>}
                                    </div>
                                </TableCell>
                                <TableCell>
                                    <div className="font-medium text-gray-900">{item.nama_produk}</div>
                                    <div className="text-xs text-gray-500">{item.satuan?.nama_satuan}</div>
                                </TableCell>
                                <TableCell>
                                    <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-blue-50 text-blue-700">
                                        {item.kategori?.nama_kategori}
                                    </span>
                                </TableCell>
                                <TableCell className="font-medium text-gray-900">
                                    {formatRupiah(item.harga_jual)}
                                </TableCell>
                                <TableCell>
                                    {item.is_tersedia ? (
                                        <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                                            Tersedia
                                        </span>
                                    ) : (
                                        <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800">
                                            Habis
                                        </span>
                                    )}
                                </TableCell>
                                <TableCell className="text-right space-x-2">
                                    
                                    {/* --- TOMBOL RESEP (BARU) --- */}
                                    <Link href={`/admin/produk-resto/${item.id}/resep`}>
                                        <Button variant="outline" size="icon" title="Atur Resep">
                                            <ChefHat className="h-4 w-4 text-orange-600" />
                                        </Button>
                                    </Link>

                                    <Button variant="ghost" size="icon" onClick={() => onEdit(item)} title="Edit Produk">
                                        <Pencil className="h-4 w-4 text-blue-600" />
                                    </Button>
                                    <Button variant="ghost" size="icon" onClick={() => onDelete(item.id)} title="Hapus Produk">
                                        <Trash2 className="h-4 w-4 text-red-600" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))
                    )}
                </TableBody>
            </Table>

            {/* Pagination Footer */}
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t">
                    <div className="text-sm text-gray-500">
                         Total: {totalData} Produk
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button
                            variant="outline"
                            size="sm"
                            onClick={() => onPageChange(currentPage - 1)}
                            disabled={currentPage === 1}
                        >
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">
                            Hal {currentPage} / {totalPages}
                        </span>
                        <Button
                            variant="outline"
                            size="sm"
                            onClick={() => onPageChange(currentPage + 1)}
                            disabled={currentPage === totalPages}
                        >
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}